.App__title
{
  height: 10rem;
  text-align: center;
}

.App__tasklist 
{
  width: 50%;
  margin: auto;
}

@media screen and (max-width: 576px){
  .App__tasklist 
{
  width: 80%;
}

}

@media screen and (min-width: 577px) and (max-width: 1200px){
  .App__tasklist 
{
  width: 60%;
}

}

.App__item {
  border: 1px solid lightgrey;
  margin-top: 2rem;
  padding: 1.5rem;
}
